Meteor.methods({
    // Called when the page is first loaded.
    getDefaultStats: () => {
        const defaultStats = Stats.findOne({ default: true });
        if (defaultStats) {
            return defaultStats;
        }
        console.log('OH NO! NO DEFAULT STATS');
        return DEFAULT_STATS;
    },

    // Given an object of filters, get counts for all of the elements NOT filtered
    //
    // @param filters Object with shape {
    //      filter1: [],
    //      filter2: [].
    //      ...
    // }
    // @return Object with shape {
    //      Area1: {
    //          Element1: 123,
    //          Element2: 234,
    //          ...
    //      },
    //      Area2: {
    //          Element3: 345,
    //          Element4: 456,
    //          ...
    //      },
    //      ...
    // }
    getStats: (filters) => {

        // See if we have stats logged for the given filters and return them if we do
        const thisWeek = new Date();// Current Date
        thisWeek.setDate(thisWeek.getDate() - 7);
        const existingStats = Stats.find({ date: { $gt: thisWeek }, ...filters }).fetch();
        if (existingStats.length) {
            console.log('Found existing stats');
            return existingStats[0].stats;
        }

        // Make the SQL Query to get the required stats
        const stats = heyLookAnotherStatsMethodTakesInFiltersOutputsStatsBlock(filters);

        //Insert the document into the Mongo Cache AFTER we return it to the
        Meteor.setTimeout(() => {
            Stats.upsert({ ...filters }, {
                date: new Date(),
                stats,
                ...filters
            });
        }, 0);
        return stats;
    }
});


