// Given an object of filters, build a SQL Query
//
// @param filters Object with shape {
//      filter1: [],
//      filter2: [].
//      ...
//  }
// @return String SQL Query to get counts
const heyLookAnotherStatsMethodTakesInFiltersOutputsStatsBlock = (filters) => {
    const query = getQueryPromise();
    const sqlQuery = `${sqlBase} ${getWhereClause(filters)}`;
    console.log('***** SQL *****\n', sqlQuery, '\n***** END SQL *****');
    const statsReturn = query(`${sqlBase} ${getWhereClause(filters)}`).await();
    // console.log('***** Return *****\n', statsReturn, '\n***** Return *****');
    const statsBlock = getStatsFromSQLResponse(statsReturn);
    // console.log('....StatsBlock.....\n', statsBlock);
    return statsBlock;
};
const DBNameMap = {
    'Enterprise Application Platform': 'Enterprise_Application_Platform'
}
const sqlBase = 'SELECT sum(count) AS count, sum(`NA`) AS `NA`, sum(`LATAM`) AS `LATAM`, sum(`EMEA`) AS `EMEA`, sum(`APAC`) AS `APAC`, sum(`IT Manager`) AS `IT Manager`, sum(`Other`) AS `Other`, sum(`Lead Developer`) AS `Lead Developer`, sum(`IT Executive`) AS `IT Executive`, sum(`Business Analyst`) AS `Business Analyst`, sum(`System Administrator`) AS `System Administrator`, sum(`Architect`) AS `Architect`, sum(`IT Decision Maker`) AS `IT Decision Maker`, sum(`Enterprise`) AS `Enterprise`, sum(`Mid-Market Enterprise`) AS `Mid-Market Enterprise`, sum(`Medium Business`) AS `Medium Business`, sum(`Small Business`) AS `Small Business`, sum(`Manufacturing`) AS `Manufacturing`, sum(`Finance`) AS `Finance`, sum(`Telecommunications`) AS `Telecommunications`, sum(`Media & Internet`) AS `Media & Internet`, sum(`Healthcare`) AS `Healthcare`, sum(`Education`) AS `Education`, sum(`Government`) AS `Government`, sum(`Inactive`) AS `Inactive`, sum(`Lapsed`) AS `Lapsed`, sum(`Most Active`) AS `Most Active`, sum(`Lapsing`) AS `Lapsing`, sum(`Invalid`) AS `Invalid`, sum(`Internal`) AS `Internal`, sum(`Accelerate`) AS `Accelerate`, sum(`Integrate`) AS `Integrate`, sum(`Efficiency`) AS `Efficiency`, sum(`Transitional`) AS `Transitional`, sum(`Agility`) AS `Agility`, sum(`Training`) AS `Training`, sum(`RHEL`) AS `RHEL`, sum(`Satellite`) AS `Satellite`, sum(`RHEV`) AS `RHEV`, sum(`Enterprise_Application_Platform`) AS `Enterprise_Application_Platform`, sum(`OpenStack`) AS `OpenStack`, sum(`CloudForms`) AS `CloudForms` FROM contact_counts ';
const getWhereClause = (filters) => {
    const where = Object.keys(filters)
        .filter(k => filters[k].length)
        .map(k => {
            const thisArr = filters[k];
            return `(${thisArr
                .map(val => DBNameMap[val] || val)
                .map(val => `\`${val}\`>0`)
                .join(' OR ')})`;
        }).join(' AND ');
    if (where.length) {
        return `WHERE ${where};`;
    }
    return ';';
};
